body {
  margin: 0;
  padding:15px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  min-height: 450px;
  height: 100vh;
  margin: 0;
  background: #282828;
  color: #fff;
  font-family: "Nunito", sans-serif;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

.todoList {
  width: 320px;
  height: 500px;

  margin: 0 auto;
  border-radius: 10px;
  box-shadow: 0 7px 30px rgba(75, 70, 6, 0.3);
}

.add {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  padding: 10px 20px;
  border-bottom: 1px solid #cccccc;
}

.add > *,
.todo-item > * {
  background: transparent;
  border: none;
  height: 35px;
}

.add input[type="text"],
.todo-item input[type="text"] {
  padding: 0 5px;
  font-weight: 700;
  font-size: 1.2rem;
  color: #c7c7c7;
  outline: none;
}

.add .input-buttons a {
  text-decoration: none;
}

.add .input-buttons i {
  margin-top: 5px;
  font-size: 20px;
  color: #eaeaea;
}

.align {
  padding: 0;
}

.todo-list li {
  user-select: none;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
}

.todo-list li i {
  color: #c3c3c3;
  font-size: 15px;
  cursor: pointer;
  padding: 5px 10px;
}

.todo-list input[type="checkbox"] {
  display: none;
}

.todo-list input[type="checkbox"] + label {
  color: #ffffff;
  font-size: 15px;
  cursor: pointer;
  position: relative;
  border-radius: 3px;
  display: inline-block;
  padding: 5px 5px 5px 40px;
}

.todo-list input[type="checkbox"] + label:hover {
  color: #bebebe;
  background-color: #3e3e3e;
}

.todo-list input[type="checkbox"] + label span.check {
  left: 4px;
  top: 50%;
  position: absolute;
  transform: translatey(-50%);
  width: 18px;
  height: 18px;
  display: block;
  background: #171717;
  border-radius: 3px;
  border: 1px solid #ffb510;
  box-shadow: -2px -2px 2px rgba(67, 67, 67, 0.5),
  inset 2px 2px 4px rgba(0, 0, 0, 0.5),
  inset -2px -2px 2px rgba(67, 67, 67, 0.3), 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.todo-list input[type="checkbox"]:checked + label {
  color: #e8e8e8;
  text-decoration: line-through;
}

.todo-list input[type="checkbox"]:checked + label span.check {
  background-color: transparent;
  border-color: transparent;
  box-shadow: none;
}

.todo-list input[type="checkbox"] + label span.check::after {
  width: 100%;
  height: 100%;
  content: "";
  display: block;
  position: absolute;
  background-image: url("https://www.freepnglogos.com/uploads/tick-png/check-mark-tick-vector-graphic-21.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 16px;
  transform: scale(0);
  transition: transform 300ms cubic-bezier(0.3, 0, 0, 1.5);
}

.todo-list input[type="checkbox"]:checked + label span.check::after {
  transform: scale(1);
}

.todo-list input[type="checkbox"] + label span.check::before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  content: "";
  position: absolute;
  border-radius: 50%;
  background: #d9d9d9;
  opacity: 0.8;
  transform: scale(0);
}

.todo-list input[type="checkbox"]:checked + label span.check::before {
  opacity: 0;
  transform: scale(1.3);
  transition: opacity 300ms cubic-bezier(0.2, 0, 0, 1),
  transform 400ms cubic-bezier(0.3, 0, 0, 1.4);
}

.plus-icon {
  color:  #ffb510;
}

.todo-buttons {
  display: flex;
}

.edit-item input[type="text"]{
  width: 100%;
  height: 30px;
  border: none;
  border-bottom: 1px solid;
  background: transparent;
}

.input-buttons .add-todo {
  border: none;
  background: transparent;
  cursor: pointer;
}

.input-buttons i {
  border: none;
}